    /**
    * 
    */
    convertToTable = (orgTree, data) => {
        let count = 0;
        orgTree.forEach((item, index) => {
            if (item.pid == '~' || item.pid == undefined) {
                count = count + 1;
            }
            data.push(item);
            if (item.children && item.children.length > 0) {
                this.convertToTable(item.children, data);
            }
        });
        return count;
    }

