    onSortClick() {
        let pk_saleappendstru = this.props.syncTree.getSelectNode(leftTree).id;
        ajax({
            url: ajaxUrl.sasmemb4adjustUrl,
            method: "post",
            data: { pk_saleappendstru: pk_saleappendstru },
            success: (res) => {
                if (res.success) {
                    //
                    if (res.data) {
                        let treeData = this.dealTreeData(res.data);
                        this.props.syncTree.setSyncTreeData(memberSortTreeId, treeData);
                    } else {
                        this.props.syncTree.setSyncTreeData(memberSortTreeId, []);
                    }
                    this.props.modal.show(memberSortModalId, { title: this.state.json['10100SAS-000020'] });
                }
            }
        });
    }

    onMouseEnterSortTreeEve(key) {
        let obj = {
            delIcon: false,
            editIcon: false,
            addIcon: false
        };
        this.props.syncTree.hideIcon(memberSortTreeId, key, obj)
    }

    setOrder(orderType) {
        let selected = this.props.syncTree.getSelectNode(memberSortTreeId)
        if (!selected) {
            toast({ content: this.state.json['10100SAS-000009'], color: 'warning' });/*  */
        }
        let pk_sasmember = selected.id;
        ajax({
            url: ajaxUrl.setordertreenodeUrl,
            method: "post",
            data: { pk_sasmember: pk_sasmember, orderType: orderType },
            success: (res) => {
                if (res.success) {
                    //
                    let treeData = this.dealTreeData(res.data);
                    this.props.syncTree.setSyncTreeData(memberSortTreeId, treeData);
                    this.props.modal.show(memberSortModalId, { title: this.state.json['10100SAS-000020'] });
                }
            }
        });
    }

    buttonClick(props, id) {
        let _this = this;
        switch (id) {
            case 'add':
                props.form.EmptyAllFormValue(formId)
                _this.onAdd();
                _this.toggleShow(this.props);
                break;
            case 'copy':
                _this.onCopy(); break;
            case 'enable':
                _this.onEnable();
                break
            case 'disable':
                _this.onDisable();
                break
            case 'import':
                _this.onImportClick();
                break
            case 'adjust':
                _this.onSortClick();
                break
            case 'addline':
                _this.props.cardTable.addRow(prodlines);
                break
            case 'upTop':
                //
                this.setOrder('upTop');
                break;
            case 'upOne':
                //
                this.setOrder('upOne');
                break;
            case 'downOne':
                //
                this.setOrder('downOne');
                break;
            case 'downBottom':
                //
                this.setOrder('downBottom');
                break;
            case 'upgrade':
                //
                this.setOrder('upgrade');
                break;
            case 'degrade':
                //
                this.setOrder('degrade');
                break;
            case 'refresh':
                //
                setDefData('saleappendstru_btnopr', props.config.datasource, 'refresh');
                this.loadLeftTreeData(false);
                break;
            case 'print':
                let pks = cacheTools.get('saspks');
                print(
                    'pdf',  //: 'html', 'pdf'pdf
                    ajaxUrl.printUrl,
                    {
                        //billtype:'',  //
                        funcode: props.config.appcode,      //
                        nodekey: 'saleappendstruCardPrint',     //
                        oids: pks    //['1001A41000000000A9LR','1001A410000000009JDD']  pk  oids,
                    }
                );
                break;
            case 'export':
                let pks1 = cacheTools.get('saspks');
                // this.setState({
                //     pks: pks1
                // }, this.refs.printOutput.open());
                var data={
                    funcode: '10100SAS',      //
                    nodekey: 'saleappendstruCardPrint',     //
                    oids: pks1,    //['1001A41000000000A9LR','1001A410000000009JDD']  pk  oids,
                    outputType: "output"
                }
                output({data: data,url:ajaxUrl.printUrl});
                break;
            default:
                break
        }
    }

